"use client";

import { useCreateUser } from "@/lib/utils/hooks/user.mutations.hooks";
import { useUserStore } from "@/store/user.store";
import {
  User,
  UserAcademic,
  UserAddress,
  UserContact,
  UserInfo,
} from "@/types/user.types";
import { useState } from "react";
import { useForm } from "react-hook-form";

const steps = [
  "User Info",
  "Contact Info",
  "Address",
  "Academic Background",
  "Review",
];

export default function MultiStepForm() {
  const { user, setUser } = useUserStore();
  const [step, setStep] = useState(0);
  const { register, handleSubmit,watch, getValues } = useForm<User>({
    defaultValues: user ?? {},
  });

//   create mutation
const createMutation = useCreateUser()

  const onSubmit = (data: User) => {
    setUser(data)
    if (step < steps.length - 1) {
      setStep(step + 1);
    } else {
        createMutation.mutate(data)
      alert("User added successfully!");
    }
  };

  return (
    <div className="max-w-lg mx-auto p-6 bg-white shadow-md rounded-md">
      <h2 className="text-xl font-bold">{steps[step]}</h2>

      <form onSubmit={handleSubmit(onSubmit)} className="mt-4 space-y-4">
        {step === 0 && (
          <>
            <input
              {...register("userInfo.firstName", { required: true })}
              placeholder="First Name"
              className="border p-2 w-full"
            />
            <input
              {...register("userInfo.lastName", { required: true })}
              placeholder="Last Name"
              className="border p-2 w-full"
            />
            <input
              {...register("userInfo.dob", { required: true })}
              type="date"
              placeholder="DOB"
              className="border p-2 w-full"
            />
            <input
              {...register("userInfo.occupation", { required: true })}
              placeholder="Occupation"
              className="border p-2 w-full"
            />
            <select
              {...register("userInfo.gender", { required: true })}
              className="border p-2 w-full"
            >
              <option value="">Select Gender</option>
              <option value="Male">Male</option>
              <option value="Female">Female</option>
            </select>
          </>
        )}

        {step === 1 && (
          <>
            <input
              {...register("userContact.email", { required: true })}
              type="email"
              placeholder="Email"
              className="border p-2 w-full"
            />
            <input
              {...register("userContact.phone", { required: true })}
              type="tel"
              placeholder="Phone"
              className="border p-2 w-full"
            />
            <input
              {...register("userContact.fax")}
              placeholder="Fax (optional)"
              className="border p-2 w-full"
            />
            <input
              {...register("userContact.linkedInUrl")}
              placeholder="LinkedIn URL (optional)"
              className="border p-2 w-full"
            />
          </>
        )}

        {step === 2 && (
          <>
            <input
              {...register("userAddress.address", { required: true })}
              placeholder="Address"
              className="border p-2 w-full"
            />
            <input
              {...register("userAddress.city", { required: true })}
              placeholder="City"
              className="border p-2 w-full"
            />
            <input
              {...register("userAddress.state", { required: true })}
              placeholder="State"
              className="border p-2 w-full"
            />
            <input
              {...register("userAddress.country", { required: true })}
              placeholder="Country"
              className="border p-2 w-full"
            />
            <input
              {...register("userAddress.zipCode", { required: true })}
              placeholder="Zip Code"
              className="border p-2 w-full"
            />
          </>
        )}

        {step === 3 && (
          <>
            <textarea
              {...register("userAcademic.pastSchools", { required: true })}
              placeholder="List of past schools"
              className="border p-2 w-full"
            />
          </>
        )}

        {step === 4 && (
          <div className="p-4 bg-gray-100 rounded-md">
            <p>
              <strong>Name:</strong> {watch("userInfo.firstName")}{" "}
              {watch("userInfo.lastName")}
            </p>
            <p>
              <strong>Email:</strong> {watch("userContact.email")}
            </p>
            <p>
              <strong>Phone:</strong> {watch("userContact.phone")}
            </p>
            <p>
              <strong>Address:</strong> {watch("userAddress.address")},{" "}
              {watch("userAddress.city")}, {watch("userAddress.state")}
            </p>
            <p>
              <strong>Education:</strong> {watch("userAcademic.pastSchools")}
            </p>
          </div>
        )}

        <div className="flex justify-between">
          {step > 0 && (
            <button
              type="button"
              onClick={() => setStep(step - 1)}
              className="px-4 py-2 bg-gray-500 text-white rounded-md"
            >
              Back
            </button>
          )}

          <button
            type="submit"
            className="px-4 py-2 bg-blue-500 text-white rounded-md"
          >
            {step < steps.length - 1 ? "Next" : "Submit"}
          </button>
        </div>
      </form>
    </div>
  );
}
